import React from 'react';
import Link from 'gatsby-link';
import Radium from 'radium';

import sharedStyles from '../shared/sharedStyles';

import ProjectScreenshot from './projectScreenshot';

import SectionHeading from "./sectionHeading";

import reebok1 from '../assets/work/reebok1.jpg';
import buffalo1 from '../assets/work/buffalo7-1.png';
import federal1 from '../assets/work/federal1.jpg';
import openmarket1 from '../assets/work/openmarket1.jpg';
import jamie1 from '../assets/work/jamie1.jpg';
import festivalofyou1 from '../assets/work/festivalofyou1.jpg';
import mercury1 from '../assets/work/mercury1.jpg';

import ecommerce from '../assets/ecommerce.png';
import educational_institutions from '../assets/educational_institutions.png';
import local_business from '../assets/local_business.png';
import marketing from '../assets/Marketing.png';
import portfolio from '../assets/portfolio.png';
import organization from '../assets/ORGANIZATION.png';

import DottedCircle from './decorations/dottedCircle';
import Circle from './decorations/circle';
import Triangle from './decorations/triangle';

class WorkShelf extends React.Component {

  componentDidMount(){

    if(typeof window !== "undefined"){

      const ScrollMagic = require('ScrollMagic');
      typeof window !== 'undefined' && require('animation.gsap');
      // require('debug.addIndicators');
      const TimelineMax = require('TimelineMax');

      const controller = new ScrollMagic.Controller();

      const tween = new TimelineMax();

      tween.add([
        TweenMax.to("#featured-image", 1, {x: 100, ease: Linear.easeNone}),
        TweenMax.to("#featured-image", 1, {opacity: 1, ease: Linear.easeNone})
        ]);

      var scene = new ScrollMagic.Scene({triggerHook: 0.5, triggerElement: "#work-section", duration: 200})
              .setTween(tween)
              // .setPin("#header-pin", {pushFollowers: true})
              // .addIndicators()
              .addTo(controller);

      const tween2 = new TimelineMax();

      tween2.add([
        TweenMax.to("#featured-info", 1, {y: 100, ease: Linear.easeNone}),
        TweenMax.to("#featured-info", 1, {opacity: 1, ease: Linear.easeNone})
        ]);

      var scene = new ScrollMagic.Scene({triggerHook: 0.4, triggerElement: "#work-section", duration: 200})
              .setTween(tween2)
              // .setPin("#header-pin", {pushFollowers: true})
              // .addIndicators()
              .addTo(controller);

      const tweenWorkProjects = new TimelineMax();

      tweenWorkProjects.add([
        TweenMax.to("#featured-info", 1, {y: 100, ease: Linear.easeNone}),
        TweenMax.to("#featured-info", 1, {opacity: 1, ease: Linear.easeNone})
        ]);

      var scene = new ScrollMagic.Scene({triggerHook: 0.4, triggerElement: "#work-section", duration: 200})
              .setTween(tweenWorkProjects)
              // .setPin("#header-pin", {pushFollowers: true})
              // .addIndicators()
              .addTo(controller);
    }
            

  }

  render(){

    return (<section id="work-section" className="diagonal clockwise" style={styles.section}>
      <SectionHeading title="Some Of Our Work Area" smallText="Have a look at" />

      <div className="container-fluid" style={styles.container}>
        <div className="row no-gutters">
          <div className="col-sm-6">
            <div id="featured-image" style={styles.shadowImage}>
              <span style={styles.imageBefore}></span>
              <Link to="#"><img src={reebok1} style={styles.featuredImage} /></Link>
            </div>
          </div>
          <div id="featured-info" className="col-sm-6" style={styles.rightHalf}>
            <Link to="#" className="specialHeading" style={{fontSize: "18px"}}>About Us</Link>
            <p style={styles.p}>We are providing business web services from website designing to marketing. We are located into pune. Our webtools help you in different business areas which includes webapps and marketing tools</p>
          </div>
        </div>
      </div>

      <div className="container-fluid" style={styles.projectsContainer}>
        <div className="row">
          <div id="project1" style={styles.screenshot} className="col-xs-12 col-sm-4"><Link to="#"><ProjectScreenshot screenshotURL={ecommerce} description="E-Commerce" /></Link></div>
          <div id="project2" style={styles.screenshot} className="col-xs-12 col-sm-4"><Link to="#"><ProjectScreenshot screenshotURL={portfolio} description="Portfolio;" /></Link></div>
          <div className="clearfix visible-xs-block"></div>
          <div id="project3" style={styles.screenshot} className="col-xs-12 col-sm-4"><Link to="#"><ProjectScreenshot screenshotURL={educational_institutions} description="Educational Institutions" /></Link></div>
          <div className="clearfix visible-sm-block visible-md-block"></div>
          <div id="project4" style={styles.screenshot} className="col-xs-12 col-sm-4"><Link to="#"><ProjectScreenshot screenshotURL={local_business} description="Local Business" /></Link></div>
          <div className="clearfix visible-xs-block"></div>
          <div id="project5" style={styles.screenshot} className="col-xs-12 col-sm-4"><Link to="#"><ProjectScreenshot screenshotURL={marketing} description="Marketing" /></Link></div>
          <div id="project6" style={styles.screenshot} className="col-xs-12 col-sm-4"><Link to="#"><ProjectScreenshot screenshotURL={organization} description="Organization" /></Link></div>
        </div>
        <Circle style={{position: "absolute", top: "-35vh", right: "-5vh"}} diameter="20" colour="#3a86ff" />
        <Triangle style={{position: "absolute", top: "20vh", left: "-4vh", zIndex: -1}} width="12" height="15" colour="#fdc523" rotate="45" />
      </div>

    </section>);
  }

}

var styles = {};

styles.section = {
  background: "white",
  position: "relative"
}

styles.container = {
  marginTop: "3%",
  padding: 0,
  marginBottom: "7%"
}

styles.featuredImage = {
  width: "100%",
  position: "relative",
  zIndex: 1
}

styles.shadowImage = {
  position: "relative",
  left: "-100px",
  opacity: 0
}

styles.imageBefore = {
  position: "absolute",
  width: "102%",
  height: "92%",
  top: "-6%",
  left: 0,
  background: "#a8e4bc",
  zIndex: -1
}

styles.rightHalf = {
  padding: "3% 6%",
  position: "relative",
  top: "-100px",
  opacity: 0
}

styles.projectsLink = {
  textAlign: "center",
  marginTop: "3%",
  fontSize: "3vh"
}

styles.p = {
  fontFamily: "Apercu-Regular",
  fontSize: "20px",
  color: "#b2b1b8",
  lineHeight: "1.7"
}

styles.projectsContainer = {
  position: "relative",
  paddingBottom: "8%"
}

styles.screenshot = {
  paddingLeft: "10px",
  paddingRight: "10px"
}

export default Radium(WorkShelf);




